@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Roboto', sans-serif;
	color: #303030;
	background: #f2f2f2;
}
.countdown-container {
    display: flex;
    justify-content: space-between;
    width: 200px; /* ajusta según tus necesidades */
}

.countdown-item {
    text-align: center;
    margin: 5px; /* ajusta según tus necesidades */
}

.countdown-item p {
    font-size: 18px; /* ajusta según tus necesidades */
}

.countdown-item span {
    font-size: 12px; /* ajusta según tus necesidades */
}

